//Main_ css
#mainVisualSlider{
    background-color: aliceblue;
    width: 100%;
    height: auto;
    aspect-ratio: 1920 / 747;
    .slick-slider{
        width: 100%;
        height: 100%;
        position: relative;
        button{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            z-index: 2;
            background-color: unset;
            border: 0;
            font-size: 0;
        }
        button.slick-prev{left: 5rem;background: url(../public/main_prev.png)no-repeat center center/cover;width: 3.3rem;height: 6.2rem;}
        button.slick-next{right: 5rem;background: url(../public/main_next.png)no-repeat center center/cover;width: 3.3rem;height: 6.2rem;}
        .slick-list{
            height:100%;
            .slick-track{
                height: 100%;
                 div{
                    height: 
                    100%;
                .mainVisualImg{
                    width: 100%;
                    height: 100%;
                    display: flex!important;
                    flex-wrap: wrap;justify-content: center;align-items: center;
                    .mainVisualInner {
                    max-width: 144rem;
                    width: 100%;
                    height: max-content;
                    h3{
                        font-size: 7.1rem;
                        font-weight: 800;
                        color: #fff;
                        width: 48.7rem;
                        line-height: 1.2;
                        margin-bottom: 6rem;
                    }
                    p{
                        font-size: 3.1rem;
                        color: #fff;
                        line-height: 1.1;
                        width: 53rem;
                        font-weight: 100;
                    }
                    }
                }
                .slider01{object-fit: cover;background-repeat: no-repeat;background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5)), url(../public/main.jpg) center center;background-blend-mode: color-burn;background-size: 100%;}
                .slider02{object-fit: cover;background-repeat: no-repeat;background-size: cover;background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5)), url(../public/img1.jpg) center center}
                .slider03{object-fit: cover;background-repeat: no-repeat;background-size: cover;background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5)), url(../public/img2.jpg) center center}
                .slider04{object-fit: cover;background-repeat: no-repeat;background-size: cover;background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.5)), url(../public/img3.jpg) center center}
                }
                
            }
        }
    }
}
#middleCont{
    padding: 16rem 0;
    .inner{
        display: grid;
        grid-template-columns: repeat(auto-fit,minmax(34rem, 1fr));
        gap: 2.4rem;
        width: 100%;
        .cardWrap{
            padding-bottom: 3rem;
            box-shadow: 0.1rem 0.7rem 1.4rem rgba(37, 37, 37, 0.3);
            overflow: hidden;
            border-radius: .6rem;
            min-height: 48.3rem;
            position: relative;
            cursor: pointer;
            &:hover{
                background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.9)),  url(../public/main.jpg)no-repeat center center/cover;background-blend-mode: hue;
                color: #fff;
                dl{
                    dt{
                        color: #fff;
                    }
                    dd{
                        color: #fff;
                    }

                }
                figure{
                    img{
                        scale: 1.5;
                    }
                }
            }
            figure{
                width:100%;
                height: 192px;
                /* aspect-ratio: 340 / 192; */
                overflow: hidden;
                img{
                    object-fit: cover;
                }
            }
            dl{
                padding-top: 3rem;
                padding-left: 3rem;
                padding-right: 3rem;      
                dt{
                    font-size: 3.1rem;
                    font-weight: 700;
                    color: #1c1d1c;
                    line-height: 1.2;
                    margin-bottom: 1.6rem;
                }
                dd{
                    font-size: 1.8rem;
                    line-height: 1.5;
                    color: #424242;
                }
                dd:last-child{
                    font-size: 1em;
                    color: #bdbdbd;
                    position: absolute;
                    bottom: 3rem;
                }
            }
        }
    }


}
#lastCont{
    background-color: #f5f5f5;
    min-height: 60rem;
    #customCont{
        position: relative;
        height: 100%;
        min-height: inherit;
        display: flex;
        flex-wrap: wrap;
        .customcont{
            height: 100%;
            min-height: inherit;
        }
        .leftArea{
            width: 100%;
            background-color: #0f5337;
            position: absolute;
            z-index: 1;
            
            .inner{
                // max-width: unset;
                min-height: inherit;
                display: flex;
                flex-wrap: wrap;
                flex-direction: column;
                justify-content: center;
                width: 100%;
                h3{
                    font-size: 4em;
                    color: #fff;
                    font-weight: 700;
                    margin-bottom: 5%;
                    width: max-content;
                    border-bottom: .1rem solid #ddd;
                    padding-bottom: 5%;
                }
                ul{
                    width: max-content;
                    display: flex;
                    flex-wrap: wrap;
                    flex-direction: column;
                    row-gap: 1em;
                    li{
                        display: block;
                        a{
                            font-size: 1.4em;
                            color: #f5f5f5;
                            *{
                                letter-spacing: -1px;
                            }
                        }
                    }
                }
            }
        }
        .rightArea{
            z-index: 2;
            position: absolute;
            top: 0;
            right: 0;
            background: #f5f5f5;
            width: calc(100% - 730px);
            ul{
                display: flex;
                flex-wrap: wrap;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                row-gap: 15px;
                height: 100%;
                li{
                    transition: 1s ease-in-out;
                    &:hover{
                        background-image: linear-gradient(to right, #0f5337, rgb(59, 155, 115));
                        transition: 1s ease-in-out;
                        
                        a{
                            color: #fff!important;
                            .date{
                                span{
                                    color: #fff;
                                }
                                b{
                                    color: #fff;
                                }
                                
                            }
                            .news_txt{
                                b{
                                    color: #fff;
                                }
                                span{
                                    color: #fff;
                                }
                            }
                        }
                    }
                    padding:  1vw;
                    display: flex;
                    flex-wrap: wrap;
                    width: calc(100% - 5vw);
                    border-radius: 1rem;
                    a{
                        display: flex;
                        flex-wrap: wrap;
                        width: 100%;
                        align-items: center;
                        
                        .date{
                            flex: 1.5;
                            text-align: center;
                            span{
                                font-size: 1em;
                                color: #555;
                                display: block;
                                padding-bottom: 5px;
                                letter-spacing: 0;
                            }
                            b{
                                font-size: 1.3em;
                                font-weight: 700;
                            }
                        }
                        .news_txt{
                            flex: 7;
                            b{
                                font-size: 1.5em;
                                font-weight: 500;
                                color: #000;
                                display: block;
                                letter-spacing: -0.4px;
                                padding-bottom: 5px;
                            }
                            span{
                                letter-spacing: 0;
                                color: #424242;
                            }
                        }
                    }
                }
            }
        }
    }
}