/* 반응형 시작 */

@media (max-width: 1480px){
    html body #root header .h_inner{padding-left: 2vw;padding-right: 2vw;box-sizing: border-box;}
    #mainVisualSlider .slick-slider .slick-list .slick-track div .mainVisualImg .mainVisualInner{max-width: unset;width: 70%;}
    html body .inner, #middleCont .inner, #lastCont #customCont .leftArea .inner{max-width: unset;width: calc(100% - 4vw);}
    #mainVisualSlider .slick-slider .slick-list .slick-track div .mainVisualImg .mainVisualInner h3{font-size: 3em;}
    #mainVisualSlider .slick-slider .slick-list .slick-track div .mainVisualImg .mainVisualInner p{font-size: 1.2em;}
    #mainVisualSlider .slick-slider .slick-list .slick-track div .slider01, #mainVisualSlider .slick-slider .slick-list .slick-track div .slider02, #mainVisualSlider .slick-slider .slick-list .slick-track div .slider03, #mainVisualSlider .slick-slider .slick-list .slick-track div .slider04{background-size: cover;}
    #lastCont #customCont .leftArea .inner h3{font-size: 3em;margin-bottom: 3%;padding-bottom: 3%;}
    #middleCont{padding-top: 5%;padding-bottom: 5%;}
    #middleCont .inner{grid-template-columns: repeat(auto-fit, minmax(2rem, 1fr));}
    #lastCont #customCont .rightArea{width: 70%;}
    html body #root header .h_inner .gnb .mainmenu.pc li a{font-size: 1em;}
    #middleCont .inner .cardWrap dl dt{font-size: 1.5em;}
    #middleCont .inner .cardWrap dl dd{font-size: 1em;}
    html body #root footer .inner h2{height: 4rem;}
    html body #root footer .inner h2 img{height: inherit;}
    html body #root footer .inner small{font-size: 1.4em;}
}

@media (max-width: 1285px){
    html body{font-size: 1.4rem;}
    #middleCont .inner{grid-template-columns: repeat(2, 1fr);}
    #lastCont #customCont .leftArea{position: static;width: 40%;}
    #lastCont #customCont .rightArea{position: static;width: 60%;display: flex;flex-wrap: wrap;flex-direction: column;justify-content: center;}
    html body #root footer{min-height: unset;padding: 5% 0 7%;}
    html body #root footer .inner{row-gap: 40%;}
}

@media (max-width: 1024px){
    #mainVisualSlider{aspect-ratio: 5/3;}
    #middleCont .inner{grid-template-columns:repeat(auto-fit, minmax(1fr, 3rem));min-height: unset;}
    #middleCont .inner .cardWrap dl dd:last-child{position: static;margin-top: 1.5em;}
    #middleCont .inner .cardWrap{min-height: unset;}
    #lastCont{min-height: unset;}
    #lastCont #customCont{min-height: unset;}
    #lastCont #customCont .leftArea{width: 100%;min-height: unset;padding-top: 3vw;padding-bottom: 3vw;}
    #lastCont #customCont .leftArea .inner{flex-direction: row;flex-wrap: wrap;justify-content: center;gap: 10%;align-items: center;}
    #lastCont #customCont .leftArea .inner h3{padding: 0;border-bottom: 0;margin-bottom: 0;font-size: 2em;}
    #lastCont #customCont .leftArea .inner ul li a{font-size: 1em;}
    #lastCont #customCont .rightArea{width: 100%;min-height: unset;height: 100%;display: block;padding-top: 5%;padding-bottom: 5%;}
    
}

/* 태블릿 */
@media (max-width: 810px){
html body #root header .h_inner .gnb .mainmenu.pc{gap: 3rem;}
html body #root footer .inner h2{height: 3rem;}
html body #root footer .inner small{font-size: 1.2em;}
}
/* 모바일 크게 */
@media (max-width: 650px){
    html body #root header .h_inner .gnb .mainmenu.pc{display: none;}
    html body #root header .h_inner .gnb .mainmenu.mo{display: block;}
    html body #root header .h_inner .gnb .mainmenu.mo ul{display: none;right: -100%;transition: 0.5s ease-in-out;top: 0;}
    html body #root header .h_inner .gnb .mainmenu.mo ul.moopen{display: flex;flex-wrap: wrap;flex-direction: column;position: fixed;top: 0;right: 0;width: calc(100% - 15%);height: 100svh;background: #fff;transition: 0.5s ease-in-out;}
    html body #root header .h_inner .gnb .mainmenu.mo ul.moopen::after{display: block;content: "";position: fixed;top: 0;left: 0;width: calc(100% - 85%);height: 100%;background: rgba(0, 0, 0, 0.5);z-index: -1;}
    html body #root header .h_inner .gnb .mainmenu.mo ul.moopen li:first-child{display: flex;flex-wrap: wrap;height: 5.3rem;align-items: center;padding-right: 2vw;}
    html body #root header .h_inner .gnb .mainmenu.mo ul.moopen li{border-bottom: .1rem solid #ddd;}
    html body #root header .h_inner .gnb .mainmenu.mo ul.moopen li a{line-height: 4rem;text-align: center;}
    html body #root header .h_inner .gnb .mainmenu.mo button.closeBtn{border: none;width: 3rem;height: 3rem;margin-left: auto;text-indent: -2px;font-weight: bold;background-color: #f5f5f5;}
    html body #root header .h_inner h1 a{max-width:10rem;}
    #mainVisualSlider .slick-slider .slick-list .slick-track div .mainVisualImg .mainVisualInner{width: max-content;}
    #mainVisualSlider .slick-slider .slick-list .slick-track div .mainVisualImg .mainVisualInner h3{font-size: 2em;width: max-content;margin-bottom: 4%;}
    #mainVisualSlider .slick-slider .slick-list .slick-track div .mainVisualImg .mainVisualInner p{width: max-content;}
    #mainVisualSlider .slick-slider button.slick-prev, #mainVisualSlider .slick-slider button.slick-next{scale: 0.5;}
    #mainVisualSlider .slick-slider button.slick-prev{left: 1vw;}
    #mainVisualSlider .slick-slider button.slick-next{right: 1vw;}
    #middleCont .inner{grid-template-columns:1fr}
    #lastCont #customCont .rightArea ul li{padding: 4vw 1vw;}
}
@media (max-width: 500px){
    #mainVisualSlider .slick-slider button.slick-prev, #mainVisualSlider .slick-slider button.slick-next{top: unset;bottom: 3%;scale: 0.35;}
    #mainVisualSlider .slick-slider button.slick-prev{left: unset;right: 8rem;}
    #lastCont #customCont .rightArea ul li a .date{flex: 3;}
    #lastCont #customCont .leftArea .inner h3{font-size: 1.5em;}
}
/* 모바일 */
@media (max-width: 375px){
    html body .inner, #middleCont .inner, #lastCont #customCont .leftArea .inner{width: calc(100% - 8vw);}
    #lastCont #customCont .rightArea ul li{width: calc(100% - 8vw);}
    html body{font-size: 1.3rem;}
    html body #root header .h_inner{padding-left: 4vw;padding-right: 4vw;}
    html body #root footer .inner h2{height: 2.5rem;}
html body #root footer .inner small{font-size: 1em;}
}

/* 반응형 시작 */