// reset
*{margin: 0;padding: 0;list-style: none;text-decoration: none;font-family: "Pretendard";font-weight: 400;box-sizing: border-box;line-height: 1;letter-spacing: -.25rem;word-break: keep-all;transition: 0.1s ease-in-out;}

html{
    font-size: 10px;
    body{
        font-size: 1.6rem;
    }
    img{max-width: 100%;width: 100%;}
    a{color: #333;display: block;}
    i{text-transform: none;}
    button{cursor: pointer;border: none;}
}




//color
$textColor : #333;

