// all Layout scss
html{
    scroll-behavior: smooth;
    body{
        height: 100%;
        #root{
            height: inherit;
            header{
            width: 100%;
            height: auto;
            min-height: 5.3rem;
            position: fixed;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            z-index: 99;
            background: #fff;
            .h_inner{
                width: 100%;
                height: 100%;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                justify-content: space-between;
                padding-left: 6.82vw;
                padding-right: 6.82vw;
                h1 a{max-width: 13.6rem;height: auto;}
                .gnb{
                    .mainmenu{
                        display: flex;
                        flex-wrap: wrap;
                    }
                    .mainmenu.pc{
                        gap: 4.5rem;
                        li{
                            &:hover{
                                a{
                                    color: #0f5337;
                                }
                            }
                            a{
                                font-size: 2rem;
                                letter-spacing: -1px;
                            }
                        }
                    }
                    .mainmenu.mo{
                        display: none;
                        .mobileBtn{
                            font-size: 2.3rem;
                            line-height: 1;
                            background: none;
                        }

                    }
                }
            }
            }
            main{
                padding-top: 5.3rem;
                
            }
            footer{
                background: #1c1d1c;
                min-height: 35rem;
                height: auto;
                .inner{
                    display: grid;
                    align-content: center;
                    height: auto;
                    min-height: inherit;
                    row-gap: 3rem;
                    h2{
                        width: auto;
                        height: 6.7rem;
                        img{width: auto;}
                    }
                    small{
                        color: #f5f5f5;
                        font-size: 2.1rem;
                        font-weight: 300;
                        letter-spacing: 0;
                    }
                }
            }
        }
        .inner{max-width: 144rem;margin: auto;width: 100%;}
    }
}

